import React, { useContext } from "react";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import { GlobalContext } from "./context/GlobalState";

export const IncomeStatus = () => {
	const { transactions } = useContext(GlobalContext);
	const transactionAmount = transactions.map(
		(transaction) => transaction.amount
	);
	const income = transactionAmount
		.filter((item) => item > 0)
		.reduce((acc, item) => {
			return acc + item;
		}, 0)
		.toFixed(2);
	const expanse = transactionAmount
		.filter((item) => item < 0)
		.reduce((acc, item) => {
			return acc + item;
		}, 0)
		.toFixed(2);

	return (
		<div className="status-container animate__animated animate__rotateInDownLeft animate__repeat-1  ">
			<div className="income-shadow animate__animated animate__rotateInDownLeft animate__repeat-1 animate__slow ">
				<div>
					<h4 style={{ boxShadow: "none" }}>INCOME</h4>
					<p className="money plus" style={{ boxShadow: "none" }}>
						$ {income}
					</p>
				</div>
			</div>
			<div className="expanse-shadow animate__animated animate__rotateInDownLeft animate__repeat-1 animate__slow ">
				<div>
					<h4 style={{ boxShadow: "none" }}>EXPANSE</h4>
					<p className="money minus" style={{ boxShadow: "none" }}>
						$ {expanse}
					</p>
				</div>
			</div>
		</div>
	);
};
