import React, { useState, useContext } from "react";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import { GlobalContext } from "./context/GlobalState";
import { TransactionHistory } from "./TransactionHistory";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

export const TransactionAdddetail = () => {
	const [Description, setDescription] = useState("");
	const [amount, setAmount] = useState("");
	const { addTransaction } = useContext(GlobalContext);
	const { transactions } = useContext(GlobalContext);
	const onSubmit = (e) => {
		e.preventDefault();

		const newTransaction = {
			id: new Date().getTime(),
			Description,
			amount: +amount,
		};

		addTransaction(newTransaction);
	};
	const difftoast = () => {
		toast("‚úîÔ∏è Transaction Added ! ", {
			position: "top-left",
		});
	};
	return (
		<div>
			<TransactionHistory />
			<div>
				<h4 className="thistory animate__animated animate__rotateInDownLeft animate__repeat-1">
					Add New Transaction{" "}
				</h4>

				<form onSubmit={onSubmit}>
					<div className="mb-3">
						<label
							htmlFor="exampleInputEmail1"
							className="form-label animate__animated animate__rotateInDownLeft animate__repeat-1 animate__delay-1s"
							style={{ color: "#fff" }}
						>
							Description
						</label>
						<input
							type="text"
							className="form-control animate__animated animate__rotateInDownLeft animate__repeat-1 animate__delay-1s"
							id="typeamount"
							aria-describedby="emailHelp"
							placeholder="Detail Of Transaction"
							value={Description}
							onChange={(e) => {
								setDescription(e.target.value);
							}}
						/>
					</div>
					<div className="mb-3">
						<label
							htmlFor="exampleInputPassword1"
							className="form-label animate__animated animate__rotateInDownLeft animate__repeat-1 animate__delay-1s"
							style={{ color: "#fff" }}
						>
							Transaction Amount
						</label>
						<input
							type="number"
							className="form-control animate__animated animate__rotateInDownLeft animate__repeat-1 animate__delay-1s"
							id="amount"
							placeholder="Dollar Value Of Transaction"
							value={amount}
							onChange={(e) => {
								setAmount(e.target.value);
							}}
						/>
					</div>

					<button className="btn-hover color-3 " onClick={() => difftoast}>
						ADD Transaction
					</button>

					{/* <a style={{ cursor: "pointer" }} className="cta">
						<span>Add Transaction</span>
						<svg width="13px" height="10px" viewBox="0 0 13 10">
							<path d="M1,5 L11,5"></path>
							<polyline points="8 1 12 5 8 9"></polyline>
						</svg>
					</a> */}
				</form>
			</div>
			<ToastContainer />
		</div>
	);
};
