import React, { useContext } from "react";
import { GlobalContext } from "./context/GlobalState";
import { RiDeleteBin2Fill } from "react-icons/ri";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

// import "./History.css";

export const TransactionHistory = () => {
	const { transactions } = useContext(GlobalContext);
	const { deleteTransition } = useContext(GlobalContext);
	const difftoast = (des) => {
		toast("‚ùå Transation Deleted!" + des, {
			position: "top-left",
		});
	};

	return (
		<>
			<h4 className="thistory animate__animated animate__rotateInDownLeft animate__repeat-1">
				Transaction HISTORY
			</h4>

			<ul
				id="List "
				className="list animate__animated animate__rotateInDownLeft animate__repeat-1"
			>
				{transactions.map((transaction) => {
					const sign = transaction.amount < 0 ? "-" : "+";
					return (
						<li
							key={transaction.id}
							className={transaction.amount < 0 ? "minus" : "plus"}
						>
							{transaction.Description}
							<span className="amount">
								{sign} $ {Math.abs(transaction.amount)}
							</span>
							<button
								type="button"
								id="delete-btn"
								onClick={() => {
									deleteTransition(transaction.id);
									difftoast(transaction.Description);
								}}
							>
								<RiDeleteBin2Fill />
							</button>
						</li>
					);
				})}
			</ul>
			<ToastContainer />
		</>
	);
};
